// Ð’Ñ‹Ð²Ð¾Ð´Ð¸Ñ‚ Ð¿Ñ€Ð°Ð²Ð¸Ð»Ð¾ font-face Ð´Ð»Ñ Ð¿Ð¾Ð´ÐºÐ»ÑŽÑ‡ÐµÐ½Ð¸Ñ ÑˆÑ€Ð¸Ñ„Ñ‚Ð° Ñ ÑƒÐºÐ°Ð·Ð°Ð½Ð½Ñ‹Ð¼Ð¸ Ð¿Ð°Ñ€Ð°Ð¼ÐµÑ‚Ñ€Ð°Ð¼Ð¸
@mixin font-face($family, $weightName, $weight: $fontWeightNormal, $style: normal, $woff2: true) {
  $fontName: $family;
  $fontNameSecond: spaceless($family);

  @if $style == normal {
    $fontName: $fontName + " " + $weightName;
    $fontNameSecond: $fontNameSecond + "-" + $weightName;
  } @else if $weight == 400 {
    $fontName: $fontName + " " + capitalize($style);
    $fontNameSecond: $fontNameSecond + "-" + capitalize($style);
  } @else {
    $fontName: $fontName + " " + $weightName + " " + capitalize($style);
    $fontNameSecond: $fontNameSecond + "-" + $weightName + capitalize($style);
  }

  @font-face {
    font-style: $style;
    font-weight: $weight;
    font-family: $family;
    font-display: swap;
    src: local($fontName),
         local($fontNameSecond),
         if($woff2, url("../fonts/#{to-lower-case($fontNameSecond)}.woff2") format("woff2"), null),
         url("../fonts/#{to-lower-case($fontNameSecond)}.woff") format("woff");
  }
}

// Ð¡ÐºÑ€Ñ‹Ð²Ð°ÐµÑ‚ ÑÐ»ÐµÐ¼ÐµÐ½Ñ‚ Ð¸Ð· Ð¿Ñ€ÐµÐ´ÑÑ‚Ð°Ð²Ð»ÐµÐ½Ð¸Ñ Ð¸ ÑƒÐ´Ð°Ð»ÑÐµÑ‚ ÐµÐ³Ð¾ Ð¸Ð· Ñ€Ð°Ð±Ð¾Ñ‡ÐµÐ³Ð¾ Ð¿Ñ€Ð¾Ñ†ÐµÑÑÐ°
@mixin visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
}
